.header {
  width: 100%;
  background: radial-gradient(
    109.56% 109.56% at 0% -2.94%,
    rgba(129, 129, 129, 0.185) 0%,
    rgba(25, 47, 45, 0.8) 100%
  );
  backdrop-filter: blur(4px);
}
.header > .container {
  height: 80px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.header-icon {
  width: 36px;
  height: 36px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 42px;
  cursor: pointer;
}
.sound-icon {
  background-image: url(../../assets/svg/audio.svg);
}
.snow-icon {
  background-image: url(../../assets/svg/snow.svg);
}
.header-title {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 400;
  color: #fff;
}
.header-search {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 200;
  background: radial-gradient(
    118.88% 606.86% at 0% 0%,
    rgba(36, 197, 219, 0.15) 0%,
    rgba(36, 197, 219, 0) 100%
  );
  backdrop-filter: blur(31px);
  border-radius: 30px;
  width: 254px;
  height: 36px;
  color: #fff;
  padding-left: 15px;
  margin-right: 56px;
  font-size: 16px;
  border: 1px solid #fff;
}
.header-search::placeholder {
  color: #fff;
  font-size: 16px;
  letter-spacing: 1.2px;
}
.favorite-count {
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
  background-image: url(./../../assets/favorite-count.png);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 5px;
  color: #fff;
  font-weight: 600;
}
